<template>
    <div id="loginContainer">
        <div><h2>LOGIN</h2></div>
            <form id="loginForm">
                <div>
                    <input id="userid" type="text" size="40" placeholder="이메일" v-model="userId" required><br>
                    <input id="passwd" type="password" size="40" placeholder="비밀번호" v-model="passwd" required>
                </div>

                <div class="keep_login">
                    <input id="keep_login" name="keep_login" type="checkbox" value="Y">
                    <label for="keep_login">로그인 상태 유지</label><br>
                </div>
                <div>
                    <button type="button" @click="onLogin">로그인</button><br>
                    <a @click="goJoin"><p>회원가입</p></a>
                    <a href="#"><p>아이디 &middot; 비밀번호 찾기</p></a>
                    <!--카카오톡으로 시작하기 버튼 -->
                    <!--비회원 주문 조회 버튼 -->
                    
                </div>
            </form>
    </div>
</template>
<script>
export default {
    name : 'Login',
    data(){
        return{
            userId:'',
            passwd:''
        }
    },
    computed : {
            bLogin() {
                return this.$store.state.bLogin
            }
    },
    methods : {
        onLogin(){
        this.$store.dispatch('findUser', {
            userId : this.userId,
            passwd : this.passwd

        })
        .then(()=>{
            if(this.bLogin ==true){
                // this.$store.state.bLogin = false;
                this.$router.push('/');
            }
        })
        

        },
        goJoin() {
            this.$router.push('/join');
        }
    },
    created() {
         if(this.bLogin ==true){
                // this.$store.state.bLogin = false;
                this.$router.push('/');
                console.log(this.bLogin);
            }
    }
}
</script>

<style scoped>

#loginContainer {
    margin-top: 5%;
    margin-left: 40%;
    border: 5px solid goldenrod;
    display: inline-block;
    padding: 20px;
}

#loginContainer h2 {
    margin-bottom: 5%;
}
#userid, #passwd {
    height: 30px;
    margin-bottom: 0.5%;
    padding: 3px;

}

#loginContainer button {
    margin-top: 5%;
    background-color: goldenrod;
    border: 1px solid goldenrod;
    width: 100%;
    font-size: 1.5em;
    color: white;
}

#loginContainer a:last-child{
    float: right;
}

a * {
    display: inline-block;
    color: black;
}
</style>